import { nanoid } from "nanoid"
import { BasketChildComponent } from "./basketChidComponent"
import { useSelector } from "react-redux"
import { useBasketActions } from "../../../store/actions/basketActions"


export const BasketContentComponent = () => {

    const dessertInBasketState = useSelector(state => state.dessertInBasketState)

    const {
        addDessertINBusket,
        delDessertInBasket
    } = useBasketActions()


    const basketCountArr = [
        { text: "", count: dessertInBasketState[0].count, add: () => addDessertINBusket(''), del: () => delDessertInBasket('') },
        { text: "", count: dessertInBasketState[1].count, add: () => addDessertINBusket(''), del: () => delDessertInBasket('') },
        { text: "  ", count: dessertInBasketState[2].count, add: () => addDessertINBusket('  '), del: () => delDessertInBasket('  ') },
        { text: " ", count: dessertInBasketState[3].count, add: () => addDessertINBusket(' '), del: () => delDessertInBasket(' ') },
        { text: "", count: dessertInBasketState[4].count, add: () => addDessertINBusket(''), del: () => delDessertInBasket('') },
        { text: " ", count: dessertInBasketState[5].count, add: () => addDessertINBusket(' '), del: () => delDessertInBasket(' ') },
    ]







    return <div id="BasketContentComponent">
        <table>
            <tbody>
                {basketCountArr.map(elem => {

                    return <BasketChildComponent
                        key={nanoid()}
                        text={elem.text}
                        count={elem.count}
                        addCountFunc={elem.add}
                        delCountFunc={elem.del}
                    />

                })}
            </tbody>
        </table>
    </div>
}